'use client';

import { useEffect, useState, use } from 'react';
import dynamic from 'next/dynamic';
import Link from 'next/link';
import {
    MapPin,
    Calendar,
    Target,
    ChevronLeft,
    Share2,
    Download,
    RefreshCw,
    Trophy,
    Filter,
    List,
    ChevronRight,
    ExternalLink,
    Store,
    Trash2,
    StopCircle
} from 'lucide-react';
import { useRouter } from 'next/navigation';
import { Card, Button, Badge, Progress } from '@/components/ui';

// Dynamically import Map component to avoid SSR issues with Leaflet
const MapComponent = dynamic(() => import('@/components/ui/Map'), {
    ssr: false,
    loading: () => (
        <div className="h-full w-full flex items-center justify-center bg-gray-100">
            <RefreshCw className="w-8 h-8 text-gray-400 animate-spin" />
        </div>
    ),
});

interface Result {
    id: string;
    lat: number;
    lng: number;
    rank: number | null;
    topResults: string; // JSON string
}

interface Scan {
    id: string;
    keyword: string;
    status: string;
    gridSize: number;
    radius: number;
    frequency: string;
    createdAt: string;
    centerLat: number;
    centerLng: number;
    results: Result[];
}

interface RankedBusiness {
    name: string;
    rank: number;
}

export default function ScanReportPage({ params }: { params: Promise<{ id: string }> }) {
    const router = useRouter();
    const resolvedParams = use(params);
    const [scan, setScan] = useState<Scan | null>(null);
    const [loading, setLoading] = useState(true);
    const [activeTab, setActiveTab] = useState<'map' | 'list'>('map');
    const [selectedPoint, setSelectedPoint] = useState<Result | null>(null);

    useEffect(() => {
        const fetchScan = () => {
            fetch(`/api/scans/${resolvedParams.id}`)
                .then(res => res.json())
                .then(data => setScan(data.scan))
                .catch(console.error)
                .finally(() => setLoading(false));
        };

        fetchScan();

        const interval = setInterval(() => {
            if (scan?.status === 'RUNNING' || scan?.status === 'PENDING') {
                fetchScan();
            }
        }, 5000);

        return () => clearInterval(interval);
    }, [resolvedParams.id, scan?.status]);

    if (loading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <RefreshCw className="animate-spin text-gray-400" />
            </div>
        );
    }

    if (!scan) return <div>Scan not found</div>;

    const totalPoints = scan.gridSize * scan.gridSize;
    const completedPoints = scan.results.length;

    const top3Count = scan.results.filter(r => r.rank && r.rank <= 3).length;
    const top10Count = scan.results.filter(r => r.rank && r.rank > 3 && r.rank <= 10).length;
    const avgRank = scan.results.reduce((acc, r) => acc + (r.rank || 20), 0) / (completedPoints || 1);

    const getTopResults = (jsonStr: string): RankedBusiness[] => {
        try {
            return JSON.parse(jsonStr);
        } catch (e) {
            return [];
        }
    };

    const handleDelete = async () => {
        if (!confirm('Are you sure you want to delete this report? This action cannot be undone.')) return;
        try {
            const res = await fetch(`/api/scans/${scan.id}`, { method: 'DELETE' });
            if (res.ok) router.push('/scans');
        } catch (error) {
            console.error('Delete failed:', error);
            alert('Failed to delete scan');
        }
    };

    const handleStop = async () => {
        if (!confirm('Stop this scan? Results collected so far will be saved.')) return;
        try {
            const res = await fetch(`/api/scans/${scan.id}`, {
                method: 'PATCH',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ status: 'STOPPED' })
            });
            if (res.ok) {
                setScan(prev => prev ? { ...prev, status: 'STOPPED' } : null);
            }
        } catch (error) {
            console.error('Stop failed:', error);
            alert('Failed to stop scan');
        }
    };

    return (
        <div className="max-w-[1600px] mx-auto space-y-6">
            {/* Breadcrumb & Actions */}
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-4">
                    <Link href="/scans" className="w-8 h-8 rounded-full bg-white border border-gray-200 flex items-center justify-center hover:bg-gray-50 transition-colors">
                        <ChevronLeft size={16} className="text-gray-600" />
                    </Link>
                    <div>
                        <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-3">
                            {scan.keyword}
                            <Badge variant={scan.status === 'COMPLETED' ? 'success' : 'blue'}>{scan.status}</Badge>
                        </h1>
                        <p className="text-sm text-gray-500 flex items-center gap-4 mt-1">
                            <span className="flex items-center gap-1"><MapPin size={12} /> {scan.centerLat.toFixed(3)}, {scan.centerLng.toFixed(3)}</span>
                            <span className="flex items-center gap-1"><Calendar size={12} /> {new Date(scan.createdAt).toDateString()}</span>
                        </p>
                    </div>
                </div>
                <div className="flex items-center gap-2">
                    {(scan.status === 'RUNNING' || scan.status === 'PENDING') && (
                        <Button variant="destructive" size="sm" onClick={handleStop}>
                            <StopCircle size={14} className="mr-2" /> Stop Scan
                        </Button>
                    )}
                    <Button variant="outline" size="sm" onClick={handleDelete} className="text-red-500 hover:text-red-700 hover:bg-red-50 border-red-200">
                        <Trash2 size={14} className="mr-2" /> Delete
                    </Button>
                    <Button variant="outline" size="sm"><Download size={14} className="mr-2" /> Export</Button>
                    <Button variant="outline" size="sm"><Share2 size={14} className="mr-2" /> Share</Button>
                </div>
            </div>

            {/* KPI Cards */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <Card className="p-4 flex items-center gap-4">
                    <div className="w-10 h-10 rounded-full bg-blue-100 flex items-center justify-center text-blue-700">
                        <Trophy size={20} />
                    </div>
                    <div>
                        <p className="text-sm text-gray-500 font-medium">Avg Rank</p>
                        <p className="text-2xl font-bold text-gray-900">#{Math.round(avgRank)}</p>
                    </div>
                </Card>
                <Card className="p-4 flex items-center gap-4">
                    <div className="w-10 h-10 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-700">
                        <Target size={20} />
                    </div>
                    <div>
                        <p className="text-sm text-gray-500 font-medium">Visibility</p>
                        <p className="text-2xl font-bold text-gray-900">
                            {completedPoints > 0 ? Math.round((top3Count / completedPoints) * 100) : 0}%
                        </p>
                    </div>
                </Card>
                <Card className="p-4 flex flex-col justify-center">
                    <div className="flex justify-between text-sm mb-1">
                        <span className="text-gray-500">Top 3</span>
                        <span className="font-bold text-emerald-600">{top3Count}</span>
                    </div>
                    <Progress value={top3Count} max={completedPoints || 1} size="sm" color="green" />
                </Card>
                <Card className="p-4 flex flex-col justify-center">
                    <div className="flex justify-between text-sm mb-1">
                        <span className="text-gray-500">4-10</span>
                        <span className="font-bold text-amber-600">{top10Count}</span>
                    </div>
                    <Progress value={top10Count} max={completedPoints || 1} size="sm" color="yellow" />
                </Card>
            </div>

            {/* Main Content Area */}
            <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
                {/* Main Map/List View */}
                <div className="lg:col-span-3 space-y-4">
                    <Card noPadding className="overflow-hidden h-[600px] flex flex-col border-none shadow-xl ring-1 ring-gray-200">
                        {/* Toolkit Bar */}
                        <div className="p-3 border-b border-gray-100 bg-white flex justify-between items-center z-10">
                            <div className="flex gap-1 bg-gray-100 p-1 rounded-lg">
                                <button
                                    onClick={() => setActiveTab('map')}
                                    className={`px-4 py-1.5 text-xs font-bold rounded-md transition-all ${activeTab === 'map' ? 'bg-white text-blue-600 shadow-sm' : 'text-gray-500 hover:text-gray-800'}`}
                                >
                                    Spatial View
                                </button>
                                <button
                                    onClick={() => setActiveTab('list')}
                                    className={`px-4 py-1.5 text-xs font-bold rounded-md transition-all ${activeTab === 'list' ? 'bg-white text-blue-600 shadow-sm' : 'text-gray-500 hover:text-gray-800'}`}
                                >
                                    List View
                                </button>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className="text-[10px] text-gray-400 font-mono tracking-tighter uppercase font-bold">Progress: {completedPoints}/{totalPoints} Points</span>
                                <Button variant="ghost" size="sm" className="h-8 text-xs font-bold text-gray-500 hover:text-blue-600">
                                    <Filter size={12} className="mr-1" /> Filters
                                </Button>
                            </div>
                        </div>

                        <div className="flex-1 relative bg-gray-50">
                            {activeTab === 'map' ? (
                                <MapComponent
                                    center={[scan.centerLat, scan.centerLng]}
                                    zoom={13}
                                    points={scan.results.map(r => ({ ...r, hasData: true })) || []}
                                    gridSize={scan.gridSize}
                                    onPointClick={(point) => setSelectedPoint(point as unknown as Result)}
                                />
                            ) : (
                                <div className="p-8 h-full bg-white overflow-y-auto">
                                    <table className="w-full text-left">
                                        <thead className="bg-gray-50 text-gray-500 text-[10px] uppercase font-bold tracking-wider italic">
                                            <tr>
                                                <th className="px-6 py-3">Point ID</th>
                                                <th className="px-6 py-3">Coordinates</th>
                                                <th className="px-6 py-3 text-center">Rank Found</th>
                                                <th className="px-6 py-3">Status</th>
                                            </tr>
                                        </thead>
                                        <tbody className="divide-y divide-gray-100">
                                            {scan.results.map((r) => (
                                                <tr key={r.id} className="hover:bg-gray-50 text-sm">
                                                    <td className="px-6 py-4 font-mono text-xs text-gray-400">{r.id.slice(-8)}</td>
                                                    <td className="px-6 py-4 text-gray-600">{r.lat.toFixed(4)}, {r.lng.toFixed(4)}</td>
                                                    <td className="px-6 py-4 text-center">
                                                        {r.rank ? (
                                                            <Badge variant={r.rank <= 3 ? 'success' : r.rank <= 10 ? 'warning' : 'destructive'}>
                                                                #{r.rank}
                                                            </Badge>
                                                        ) : (
                                                            <Badge>N/A</Badge>
                                                        )}
                                                    </td>
                                                    <td className="px-6 py-4">
                                                        <Badge variant="blue">Captured</Badge>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            )}
                        </div>
                    </Card>
                </div>

                {/* Dynamic Sidebar: Results Inspection */}
                <div className="space-y-6">
                    <Card className="min-h-[600px] flex flex-col p-0 overflow-hidden shadow-xl ring-1 ring-gray-200">
                        <div className="p-4 border-b border-gray-100 bg-blue-600 text-white">
                            <h3 className="font-bold flex items-center gap-2">
                                <Store size={18} />
                                Pin Inspection
                            </h3>
                            <p className="text-[10px] text-blue-100 mt-1">Select a point on the map to see GBP profiles.</p>
                        </div>

                        <div className="flex-1 overflow-y-auto">
                            {selectedPoint ? (
                                <div className="p-4 space-y-4">
                                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg border border-gray-100">
                                        <div>
                                            <p className="text-[10px] text-gray-400 font-bold uppercase">Point Rank</p>
                                            <p className="text-2xl font-black text-gray-900">#{selectedPoint.rank ?? '-'}</p>
                                        </div>
                                        <div className="text-right">
                                            <p className="text-[10px] text-gray-400 font-bold uppercase">Location</p>
                                            <p className="text-xs font-medium text-gray-600">{selectedPoint.lat.toFixed(4)}, {selectedPoint.lng.toFixed(4)}</p>
                                        </div>
                                    </div>

                                    <div className="space-y-2">
                                        <h4 className="text-xs font-bold text-gray-400 uppercase tracking-widest flex items-center gap-2 px-1">
                                            <List size={12} />
                                            Ranking Profiles
                                        </h4>
                                        <div className="space-y-1">
                                            {getTopResults(selectedPoint.topResults).length > 0 ? (
                                                getTopResults(selectedPoint.topResults).map((biz, idx) => (
                                                    <div
                                                        key={idx}
                                                        className={`
                                                    p-2.5 rounded-lg border flex items-center justify-between transition-colors
                                                    ${biz.name.toLowerCase().includes(scan.keyword.toLowerCase()) ? 'bg-blue-50 border-blue-200' : 'bg-white border-gray-100 hover:border-gray-200'}
                                                `}
                                                    >
                                                        <div className="flex items-center gap-3 overflow-hidden">
                                                            <span className={`
                                                        w-6 h-6 rounded flex items-center justify-center text-[10px] font-black
                                                        ${biz.rank <= 3 ? 'bg-emerald-100 text-emerald-700' : 'bg-gray-100 text-gray-600'}
                                                    `}>
                                                                {biz.rank}
                                                            </span>
                                                            <span className="text-xs font-semibold text-gray-800 truncate">{biz.name}</span>
                                                        </div>
                                                        <ExternalLink size={12} className="text-gray-300" />
                                                    </div>
                                                ))
                                            ) : (
                                                <div className="py-12 text-center text-gray-400">
                                                    <RefreshCw size={24} className="mx-auto mb-2 opacity-20" />
                                                    <p className="text-xs">Processing data...</p>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <div className="h-full flex flex-col items-center justify-center p-8 text-center text-gray-400">
                                    <div className="w-16 h-16 rounded-full bg-gray-50 flex items-center justify-center mb-4 ring-1 ring-gray-100">
                                        <Target size={32} className="opacity-20" />
                                    </div>
                                    <p className="text-sm font-medium text-gray-500">No Point Selected</p>
                                    <p className="text-xs mt-1">Click any circle on the map to inspect the ranking GBP profiles at that coordinate.</p>
                                </div>
                            )}
                        </div>
                    </Card>
                </div>
            </div>
        </div>
    );
}
